table{
    border-radius:4px;
    font-family: sans-serif, Calibri, Gill Sans, Gill Sans MT,  Trebuchet MS;
}
table, th, td {
    border: none;
    border-collapse: collapse;
    text-align:center;
    font-size: 12px;
}
table th{
  font-weight: 600;
  height:45px;
  background-color: #F4F4F4;
  border-top: 2px solid #D9D9D9;
}

td{
  height:35px;
}

th ,td{
    width:fit-content;
    min-width:50px;
    min-height:20px;
    padding:10px;
}

.input-field{
  width:100%;
  height:65%;
  background-color: #F4F4F4;;
  text-align: center;
  border:1px solid #D9D9D9;
  outline:none;
  box-sizing: border-box;
  padding:10px;
  border-radius:8px;
}

.input-field:focus,

.input-field:active{
  border:1px solid cadetblue;
}
